.filter-trigger__icon div:nth-child(2) {
	width: 11px;
}
.filter-trigger__icon div:nth-child(3) {
	width: 5px;
}

.filter__calc-btn {
	display: none;
	color: #fff;
	background-color: red;
	position: fixed;
	right: 20px;
	font-size: 12px;
	border: 0;
	padding: 5px 10px;
}

@media screen and (max-width: 767px) {
	.filter-trigger {
		display: flex;
	}
	.filter__calc-btn {
		display: inline-block;
	}
}

.table-price {
	text-align: right;
	margin-top: 30px;
}
.table-addCart {
	margin-bottom: 30px;
}

@media only screen and (max-width: 767px) {
	.table .table-price,
	.table .table-quantity {
		flex: none;
		max-width: 100%;
	}
	.table .table-quantity {
		display: flex;
		flex-direction: row;
		align-items: stretch;
		justify-content: space-between;
	}
	.table-quantity-left,
	.table-quantity-right {
		max-width: 50%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
	}
	.table .table-quantity-status {
		width: 100%;
		position: relative;
		bottom: 0;
		left: 0;
	}
	.table .table-price {
		background-color: transparent !important;
		min-height: auto;
		margin: 0;
	}

	.table .table-addCart {
		margin: 0;
	}

	.table .table-quantity-result {
		margin: 0;
	}
}
